.App {
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  background-color: #1e1e1e;
  color: #f3f3f3;
  padding: 2rem;
  min-height: 100vh;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-header {
  width: 100%;
  max-width: 52rem;
  margin: 0 auto;
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  letter-spacing: 0.1em;
  background: #111;
  padding: 0.5rem 1rem;
  border: 2px solid #444;
  box-shadow: inset 0 -2px 0 #222, 0 4px 10px rgba(0, 0, 0, 0.5);
}

.App-header p {
  font-size: 1rem;
  color: #aaa;
  margin-bottom: 2rem;
  letter-spacing: 0.05em;
}

form {
  margin-bottom: 2rem;
}

input[type="text"] {
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.05rem;
  padding: 0.5rem 1rem;
  width: 200px;
  background-color: #222;
  color: #f3f3f3;
  border: 2px solid #444;
  box-shadow: inset 0 -2px 0 #000;
  margin-right: 1rem;
  text-align: center;
}

button {
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.05rem;
  background-color: #444;
  color: #fff;
  padding: 0.5rem 1.25rem;
  border: 2px solid #666;
  cursor: pointer;
  box-shadow: 0 4px 0 #222;
  transition: all 0.1s ease-in-out;
}

button:hover {
  background-color: #555;
  box-shadow: 0 2px 0 #222;
  transform: translateY(2px);
}

.center-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}

.title-stack {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.train-status-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 2rem;
  width: 100%;
  max-width: 52rem;
  margin-left: auto;
  margin-right: auto;
}

.train-status-container table {
  margin: 1rem auto;
  border-collapse: collapse;
  width: 90%;
  max-width: 800px;
}

.train-status-container th,
.train-status-container td {
  border: 1px solid #444;
  padding: 0.5rem;
  text-align: center;
  font-size: 0.95rem;
}

.train-status-container th {
  background-color: #333;
  color: #fff;
}

.train-status-container tr:nth-child(even) {
  background-color: #2a2a2a;
}

/* train animation */

.train-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  position: relative;
  overflow: hidden;
}

.train-animation-center-container {
  position: relative;
  width: 600px;
  height: 300px;
}

div.workspace {
  /*General*/
  transform: scale(0.5, 0.5);
  /*Firefox*/
  -moz-transform: scale(0.5, 0.5);
  /*Microsoft Internet Explorer*/
  -ms-transform: scale(0.5, 0.5);
  /*Chrome, Safari*/
  -webkit-transform: scale(0.5, 0.5);
  /*Opera*/
  -o-transform: scale(0.5, 0.5);
}




.leftbox {
  position: absolute;
  top: 200px;
  left: 120px;
  width: 220px;
  height: 120px;
  background: rgb(149, 3, 3);
  border: solid;
  border-width: 5px;
}

.wheel-rec-1 {
  position: absolute;
  top: 140px;
  left: 25px;
  width: 95px;
  height: 8px;
  background: gray;
  border: solid;
  animation-name: wheel-move-1;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
}

.wheel-rec-2 {
  position: absolute;
  top: 106px;
  left: 85px;
  width: 95px;
  height: 8px;
  background: gray;
  border: solid;
  animation-name: wheel-move-2;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
}

.rightbox {
  position: absolute;
  top: 80px;
  left: 340px;
  width: 120px;
  height: 240px;
  background: rgb(149, 3, 3);
  border: solid;
  border-width: 5px;
}

.window {
  position: absolute;
  top: 15px;
  left: 5px;
  width: 100px;
  height: 75px;
  background: white;
  border: solid;
  border-width: 5px;
}

.roof {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 150px;
  height: 10px;
  background: rgb(149, 3, 3);
  border: solid;
  border-width: 5px;
}

.big-circle {
  position: absolute;
  top: 190px;
  left: 10px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: gray;
  border: solid;
  border-width: 10px;
}

.small-circle-front {
  position: absolute;
  top: 95px;
  left: 25px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: gray;
  border: solid;
  border-width: 10px;
}

.small-circle-rear {
  position: absolute;
  top: 95px;
  right: 35px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: gray;
  border: solid;
  border-width: 10px;
}

.semi-circle-front {
  position: absolute;
  top: 200px;
  left: 105px;
  width: 30px;
  height: 80px;
  border-radius: 50%;
  border: solid;
  border-width: 15px;
}

.arrow-down {
  position: absolute;
  top: -150px;
  left: 3px;
  width: 60;
  height: 0;
  border-left: 35px solid transparent;
  border-right: 35px solid transparent;
  border-top: 150px solid;
}

.arrow-left {
  position: absolute;
  top: 75px;
  left: -60px;
  width: 0;
  height: 10;
  border-top: 60px solid transparent;
  border-bottom: 2px solid transparent;

  border-right: 60px solid black;
}

.arrow-left-in {
  position: absolute;
  top: -40px;
  left: 16px;
  width: 0;
  height: 5;
  border-top: 34px solid transparent;
  border-bottom: 1px solid transparent;

  border-right: 35px solid gray;
}

.pipe {
  position: absolute;
  top: -130px;
  left: 20px;
  width: 25px;
  height: 120px;
  background: rgb(149, 3, 3);
  border: solid;
  border-width: 5px;
}

@keyframes wheel-move-1 {

  100% {
    transform: translateX(55px)
  }
}

@keyframes wheel-move-2 {

  100% {
    transform: translateX(-55px)
  }
}

a {
  text-decoration: none;
  background-color: #EEEEEE;
  color: #333333;
  border-top: 1px solid #CCCCCC;
  border-right: 1px solid #333333;
  border-bottom: 1px solid #333333;
  border-left: 1px solid #CCCCCC;
  border-radius: 30%;
  padding: 1%;
}

a:hover {
  background-color: #555;
}

#myInput {
  font-size: 16px; /* Increase font-size */
}

/* The switch - the box around the slider */
.toggle-labels {
  display: flex;
  align-items: center;
  gap: 0.5rem; /* Adjust spacing as needed */
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  background-color: #1e1e1e;
  color: #f3f3f3;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #306794;
}

input:focus + .slider {
  box-shadow: 0 0 1px #3d4246;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}


@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }

  .App-header h1 {
    font-size: 1.5rem;
    padding: 0.25rem 0.5rem;
  }

  .App-header p {
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
  }

  input[type="text"] {
    width: 100%;
    max-width: 300px;
    font-size: 0.95rem;
    padding: 0.5rem;
    margin-right: 0;
  }

  button {
    width: 100%;
    max-width: 300px;
    font-size: 0.95rem;
    padding: 0.5rem;
  }

  .train-status-container table {
    width: 100%;
    display: block;
    overflow-x: auto;
  }

  .train-status-container th,
  .train-status-container td {
    padding: 0.4rem;
    font-size: 0.9rem;
  }

  .train-animation-center-container {
    width: 100%;
    height: auto;
    transform: scale(0.6);
    transform-origin: top center;
  }

  div.workspace {
    transform: scale(0.4);
  }

  .title-row {
    flex-direction: column;
    gap: 0.25rem;
  }

  .station-detail {
    font-size: 0.9rem;
    margin: 0.25rem 0;
  }
}
